.fl-module-bw-woocommerce-icons .woocommerce-icon-container {
  padding-left: 0;
  list-style: none;
  display: -webkit-box;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  -webkit-box-flex: 0;
  -moz-box-flex: 0;
  -ms-box-flex: 0 1 auto;
  -ms-flex: 0 1 auto;
  flex: 0 1 auto;
  -ms-flex-direction: row;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.fl-module-bw-woocommerce-icons .woocommerce-icon {
  position: relative;
}
.fl-module-bw-woocommerce-icons .woocommerce-icon a {
  text-decoration: none;
}
.fl-module-bw-woocommerce-icons .woocommerce-icon .icon {
  transition: color 250ms;
}
.fl-module-bw-woocommerce-icons .woocommerce-icon:not(.woocommerce-icon-has-badge) .cart-icon-badge-container {
  display: none;
}
.fl-module-bw-woocommerce-icons .woocommerce-icon.woocommerce-icon-has-badge a .cart-icon-badge-container {
  position: absolute;
  top: -5px;
  right: -5px;
  width: 15px;
  height: 15px;
  text-align: center;
  border-radius: 100%;
  background-color: red;
}
.fl-module-bw-woocommerce-icons .woocommerce-icon.woocommerce-icon-has-badge a .cart-icon-badge-container .cart-icon-badge {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
